.add-btn {
  &__popup {
    width: 240px;
    height: 150px;
    background: #ffffff;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 18px;
    position: relative;
    top: -25px;
    left: 10px;
    &-colors {
      margin-top: 12px;
      display: flex;
      justify-content: space-between;

      .badge {
        width: 15px;
        height: 15px;
        cursor: pointer;
        border: 2px solid transparent;

        &.active {
          border: 2px solid #525252;
        }
      }
    }
    &-close-btn {
      position: absolute;
      top: -10px;
      right: -10px;
      cursor: pointer;
    }
  }

  .button {
    width: 100%;
    margin: 15px 0;
  }
}
